@charset "UTF-8";
/* Transitions */
/* Functions */
/* Mixins */
article,
aside,
details,
figcaption,
figure,
footer,
header,
nav,
section,
summary,
menu,
main {
  display: block; }

audio,
canvas,
video,
progress {
  display: inline-block;
  vertical-align: middle; }

audio:not([controls]),
[hidden],
template {
  display: none;
  height: 0; }

* {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
  font-size: inherit;
  line-height: inherit; }
  *::after, *::before {
    box-sizing: inherit; }

:after,
:before {
  vertical-align: inherit;
  text-decoration: inherit; }

:root {
  box-sizing: border-box;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-rendering: optimizeLegibility; }

html {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent; }

a {
  background-color: transparent; }
  a:focus {
    outline: thin dotted; }
  a:active, a:hover {
    outline: 0; }

iframe,
img,
svg {
  vertical-align: middle; }

iframe,
embed,
object {
  max-width: 100%; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

pre {
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow: auto;
  margin: 1em 0; }

nav ul,
nav ol {
  list-style: none; }

img {
  border: 0;
  -ms-interpolation-mode: bicubic; }

svg:not(:root) {
  overflow: hidden; }

fieldset,
legend {
  border: 0; }

fieldset {
  min-width: 0; }

legend {
  white-space: normal;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0; }

select {
  cursor: pointer; }
  select:-moz-focusring {
    color: transparent;
    text-shadow: 0 0 0 #000; }

button[disabled],
input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

button,
input,
select,
textarea {
  vertical-align: baseline;
  font: inherit;
  background: none;
  border-radius: 0;
  margin: 0; }

button,
input,
select {
  overflow: visible; }

input {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none; }
  input::-moz-focus-inner {
    border: 0;
    padding: 0; }
  input[type='checkbox'], input[type='radio'] {
    cursor: pointer;
    padding: 0; }
  input[type='checkbox'] {
    -moz-appearance: checkbox;
    -webkit-appearance: checkbox; }
  input[type='radio'] {
    -moz-appearance: radio;
    -webkit-appearance: radio; }
  input[type='number'] {
    width: auto; }
    input[type='number']::-webkit-outer-spin-button, input[type='number']::-webkit-inner-spin-button {
      height: auto; }

button,
input[type='button'],
input[type='reset'],
input[type='submit'] {
  appearance: button;
  -moz-appearance: button;
  -webkit-appearance: button;
  cursor: pointer; }

textarea {
  vertical-align: top;
  overflow: auto;
  resize: vertical;
  -moz-appearance: none;
  -webkit-appearance: none; }

table {
  border-collapse: collapse;
  border-spacing: 0;
  table-layout: fixed; }

progress {
  display: inline-block;
  vertical-align: baseline; }

menu {
  margin: 1em 0;
  padding: 0 0 0 40px; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

sup {
  position: relative;
  top: -0.5em;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline; }

/******* GÉNÉRAL *******/
::-moz-selection {
  background: rgba(0, 0, 0, 0.99);
  color: #fff;
  text-shadow: none; }

::selection {
  background: rgba(0, 0, 0, 0.99);
  color: #fff;
  text-shadow: none; }

html {
  font-size: 62.5%;
  /* sets the base font to 10px for easier math */
  background: #fff; }

body {
  font: 1.3em/1.5 sans-serif;
  color: #000;
  font-kerning: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.wysiwyg h1,
.h1 {
  font-size: 5rem;
  margin: 0.67em 0; }

.wysiwyg h2,
.h2 {
  font-size: 3rem;
  margin: 0.83em 0; }

.wysiwyg h3,
.h3 {
  font-size: 2.4rem;
  margin: 1em 0; }

.wysiwyg h4,
.h4 {
  font-size: 2.2rem;
  margin: 1.33em 0; }

.wysiwyg h5,
.h5 {
  font-size: 2rem;
  margin: 1.67em 0; }

.wysiwyg h6,
.h6 {
  font-size: 1.8rem;
  margin: 2.33em 0; }

p {
  margin: 1em 0; }

b,
strong {
  font-weight: 500; }

blockquote {
  margin: 1em 40px; }

ol,
ul {
  padding: 0;
  margin: 1em 0;
  list-style-type: none; }

ul ul {
  padding: 0 0 0 25px;
  margin: 1em 0; }

ol {
  counter-reset: ol-counter; }
  ol > li {
    counter-increment: ol-counter; }
    ol > li::before {
      content: counter(ol-counter) "."; }
  ol ol {
    padding: 0 0 0 25px;
    margin: 1em 0; }

img {
  max-width: 100%;
  height: auto; }

hr {
  height: 1px;
  max-width: 250px;
  border: 0;
  margin: 3em auto 5em;
  background: #000; }

svg {
  max-width: 100%; }

/******* LIENS / BOUTONS *******/
a {
  color: inherit; }
  a:active {
    filter: saturate(200%); }

/******* CLASSES GENERALES  *******/
.clearfix::after {
  content: '';
  display: table;
  clear: both; }

.align-center {
  text-align: center; }

.align-left {
  text-align: left; }

.align-right {
  text-align: right; }

.inline-b {
  display: inline-block;
  vertical-align: middle;
  transform: translateY(-0.1em); }

.full-width {
  width: 100%; }

.no-transition {
  transition: none !important; }

@media (min-width: 580px) {
  body {
    font-size: 1.5rem; }
  .wysiwyg h1,
  .h1 {
    font-size: 4rem; }
  .wysiwyg h2,
  .h2 {
    font-size: 3.2rem; }
  .wysiwyg h3,
  .h3 {
    font-size: 2.8rem; }
  .wysiwyg h4,
  .h4 {
    font-size: 2.2rem; }
  .wysiwyg h5,
  .h5 {
    font-size: 2rem; }
  .wysiwyg h6,
  .h6 {
    font-size: 1.8rem; } }

/* Grid */
.wrapper {
  overflow-x: hidden; }

.container {
  max-width: 1400px;
  padding: 0 35px;
  margin: 0 auto; }

/* Placeholders */
html.no-scroll {
  position: fixed;
  left: 0;
  right: 0; }

button:focus,
input:focus, a:focus {
  outline: thin dashed #000; }

/* Buttons */
/* Forms */
input:focus,
textarea:focus,
select:focus,
button:focus {
  outline: 0; }

input:-webkit-autofill,
textarea:-webkit-autofill {
  box-shadow: 0 0 0 1000px #fff inset; }

input:invalid,
textarea:invalid {
  box-shadow: none; }

input[type='search']::-webkit-search-cancel-button, input[type='search']::-webkit-search-decoration {
  display: none; }

select::-ms-expand {
  display: none; }

button {
  border: 0; }

